body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

ul{
    list-style-type: none;
}

.list-item div {
    font-size: 1.2em;
    text-decoration: none;
    color: #333;
}

.list-container {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    margin-top: 10px;
    padding: 0;
}

.nav-content .list-container .list-item {
    padding: 5px;
}

.nav-content .list-container .list-item:hover {
    background-color: aliceblue;
}


.beer-list-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
    margin-top: 20px;
    margin-left: 40px;
    padding: 0;
}

.beer-list-container .list-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.beer-list-container .list-container .list-item {
    width: 200px;
    height: 130px;
    margin: 10px;
}
.nav-wrapper {
    width: 250px;
    height: 100vh;
}

.nav-logo {
    width: 250px;
    margin-top: 20px;
}

.nav {
    margin-left: 20px;
}
